.menu {
  .page:has(&:target) {
    overflow: hidden;
  }

  overflow-y: hidden;
  height: 100vh;
  box-sizing: border-box;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;

  &__top {
    margin-bottom: 24px;

    @include on-tablet {
      margin-bottom: 8px;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__content {
    @include pad-inline;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 27px;
    color: $nav-text-color;
    text-decoration: none;
  }

  &__book-a-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $nav-text-color;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: $nav-text-color;
    text-decoration: none;
  }
}
